import React, { useState } from "react";
import Back from "../../Components/back";
import Day4Content from "./day4Content";
import bgImg from "./day4bgimg.png";
import Front from "./front";
import "./back.less";

const Day4 = () => {
  const [visible, setVisible] = useState(false);
  const answer: string = "Christmas";

  return (
    <>
      {visible ? (
        <Back id="day4" Context={Day4Content} backgroundImage={bgImg} />
      ) : (
        <Front answer={answer} setVisible={setVisible} />
      )}
    </>
  );
};

export default Day4;
