import React from 'react';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import NotFound from './days/day-1/notFound';
import Day0 from './days/day0/day0';
import Day1 from './days/day1/day1';
import Day2 from './days/day2/day2';
import Day3 from './days/day3/day3';
import Day4 from './days/day4/day4';
import Day5 from './days/day5/day5';
import Day6 from './days/day6/day6';
import S2D1 from './days/s2d1/s2d1';
import Fake from './days/day-1/fake';
import Fake5 from './days/day-1/fake5';
import Fake6 from './days/day-1/fake6';

const MainRouter = () => {
  const hello = 'Hello, World!';

  return (
    <BrowserRouter>
      <Switch>
        <Route exact path='/' component={NotFound} />
        <Route path='/day0' component={Day0} />
        <Route path='/day1' component={Day1} />
        <Route path='/day2' component={Day2} />
        <Route path='/day3' component={Fake} />
        <Route path='/santa' component={Day3} />
        <Route path='/day4' component={Day4} />
        <Route path='/day5' component={Fake5} />
        <Route path='/trpg' component={Day5} />
        <Route path='/edgeofinfinity' component={Day6} />
        <Route path='/day-last' component={Fake6} />
        <Route path='/s2d1' component={S2D1} />
        <Route render={(props: any) => <Redirect to='/' />} />
      </Switch>
    </BrowserRouter>
    // <>{hello}</>
  );
};

export default MainRouter;
