import { Button, Input, message, Space } from "antd";
import React, { useState } from "react";
import "./front.less";
import Title from "antd/lib/typography/Title";
import { Helmet } from "react-helmet";
import { Md5 } from "ts-md5";

export const getMd5 = (src: string): string => {
  return Md5.hashStr(src).toString();
};

const Front = (props: {
  setVisible: (status: boolean) => void;
  answer: string;
}) => {
  const { answer, setVisible } = props;

  const [passwd, setPasswd] = useState("");
  const checkPasswd = (): void => {
    console.log(passwd, getMd5(passwd));

    if (passwd === answer) {
      message.success("恭喜, 回答正确~");
      setVisible(true);
    } else {
      message.error(`？开卷考试你能给答错来`);
    }
  };

  return (
    <div id="front">
      <Helmet>
        <title>今日不解谜~</title>
      </Helmet>
      <Space direction="vertical">
        <Title level={1}>What Day is Day4? (9 letters)</Title>
        <Input
          bordered={true}
          maxLength={233}
          type="text"
          placeholder={`Christmas`}
          onChange={(e) => setPasswd(e.target.value)}
          onPressEnter={checkPasswd}
        />
        <div id="confirm">
          <Button id="inputbtn" type="primary" onClick={checkPasswd}>
            输入
          </Button>
        </div>
      </Space>
    </div>
  );
};

export default Front;
