import React from "react";

const NeteasePlayer = (props: {
  musicId: number;
  width?: number;
  height?: number;
}) => {
  const { musicId, width, height } = props;
  return (
    <iframe
      className="netease-player"
      title={`netease${musicId}`}
      width={width ?? 330}
      height={height ?? 86}
      src={`//music.163.com/outchain/player?type=2&id=${musicId}&auto=1&height=66`}
    ></iframe>
  );
};

export default NeteasePlayer;
