import React from "react";
import { Image } from "antd";
import Paragraph from "antd/lib/typography/Paragraph";

const Setu = (props: {
  pid: number;
  p?: number;
  align: "vertical" | "horizontal";
}) => {
  const { pid, p, align } = props;

  let addr: string = pid.toString();
  if (p) {
    addr += `-${p}`;
  }

  return (
    <div id="endsetu">
      <Paragraph>
        欢乐的时光总是那么短暂, 就让我们以一张setu来结束今天的晚安短信吧~
        <br />
        (P.S. 才不会告诉你点击图片可以查看大图呢~)
      </Paragraph>
      <div id="setu" className={align}>
        <Image src={`https://pixiv.cat/${addr}.jpg`} width={"100%"} />
      </div>
    </div>
  );
};

export default Setu;
