import { Button, Input, message, Space } from "antd";
import React, { useState } from "react";
import "./front.less";
import Title from "antd/lib/typography/Title";
import { Helmet } from "react-helmet";

const Front = (props: {
  setVisible: (status: boolean) => void;
  hint: string;
  answer: string;
}) => {
  const { answer, hint, setVisible } = props;

  const [passwd, setPasswd] = useState("");
  const [flag, setFlag] = useState(0);

  const checkPasswd = (): void => {
    if (passwd === answer) {
      message.success("恭喜, 密码正确~");
      setVisible(true);
    } else {
      setFlag(flag + 1);
      if (flag >= 20) {
        message.warning(`怎么还输不对啊ww 提示: ${hint}`);
      } else {
        message.error(`密码错误QAQ`);
      }
    }
  };

  return (
    <div id="front">
      <Helmet>
        <title>简单解谜..</title>
      </Helmet>
      <Space direction="vertical">
        <Title level={1}>请输入密码以查看</Title>
        <Input
          bordered={true}
          maxLength={233}
          type="text"
          placeholder={`假装这里有密码`}
          onChange={(e) => setPasswd(e.target.value)}
          onPressEnter={checkPasswd}
        />
        <div id="confirm">
          <Button id="inputbtn" type="primary" onClick={checkPasswd}>
            输入
          </Button>
        </div>
      </Space>
    </div>
  );
};

export default Front;
