import React from "react";
import { Helmet } from "react-helmet";

const Back = (props: { id: string; Context: any; backgroundImage: string }) => {
  let { id, Context, backgroundImage } = props;
  return (
    <div id={id}>
      <div className="back">
        <div id="backimg">
          <img src={backgroundImage} alt="background"></img>
        </div>
        <Helmet>
          <title>Switchの晚安短信~</title>
        </Helmet>
        <div id="main">
          <Context />
        </div>
      </div>
    </div>
  );
};

export default Back;
