import React, { useState } from "react";

const Secret = (props: { content: any }) => {
  let { content } = props;

  const [visible, setVisible] = useState(false);

  return (
    <div
      className={!visible ? "secret-hide" : "secret-show"}
      onClick={() => setVisible(!visible)}
    >
      {content}
    </div>
  );
};
export default Secret;
