import React, { useState, useEffect } from 'react';
import { Divider } from 'antd';
import Paragraph from 'antd/lib/typography/Paragraph';
import Title from 'antd/lib/typography/Title';
import Hid from '../../Components/hid';
import Text from 'antd/lib/typography/Text';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const FakeDay6 = () => {
  const [count, setCount] = useState(0);
  useEffect(() => {
    setTimeout(() => {
      setCount(count + 1);
      if (count > 6) {
        document.title = '/edgeofinfinity';
      }
    }, 1000);
  });

  return (
    <div className='fd5'>
      <Helmet>
        <title>最伟大的美德 是耐心~</title>
      </Helmet>
      <Title>Day ∞</Title>
      <Divider>好像有哪里不对…… 是哪里不对呢……</Divider>
      <Paragraph>
        哦，我亲爱的上帝啊，你已经学会提前查看<Link to='/trpg'>晚安短信</Link>
        了！真棒！我真为你高兴，我的孩子。
      </Paragraph>
      <Paragraph>
        但是很抱歉。额，我是说，孩子，你来错地方了。这里没有你要找的
        <Link to='/trpg'>晚安短信</Link>，真的没有……
      </Paragraph>
      <Paragraph>
        <Hid content={'什么你说你是来找涩图的？'} />
        <Hid content={<Text delete>那你快爬~</Text>} />
        <Hid content={'那要让你失望了，这里连涩图都没有。'} />
      </Paragraph>
      <Divider>嗯。是的。就这些。</Divider>
      <Divider>不要怀疑。你没有看错。</Divider>
      <Divider>这就是底线了。</Divider>
    </div>
  );
};

export default FakeDay6;
